.popup__title {
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    margin: 0 0 50px;
    padding: 0;
    color: #000000;
    align-self: flex-start;
}


@media screen and (max-width: 642px) {
    .popup__title {
        font-size: 18px;
        line-height: 22px;
        margin: 25px 22px 75px;
    }
}